.home
{
    .inner
    {
        height: 100%;
        display: flex;
        flex-direction: column;
        .home__banner 
        {
            width: 100%;
            margin: auto;
            p 
            {
                font-size: 24px;
                font-weight: 600;
                color: #3e3e3e;
                text-align: center;
            }
        }
        p
        {
            text-align: center;
            font-size: 28px;
            font-weight: 600;
            line-height: 1.5;
            color: #222;
        }
    }   
}

@media screen and (max-width: 540px)
{
    .home
    {
        .inner
        {
            .home__banner 
            {
                p 
                {
                    font-size: 18px;
                }
            }
        }
    }
} 

